import React from 'react';
import { useState, useImperativeHandle } from 'react';

const TestComponent = React.forwardRef((props, ref) => {
    const [name, setName]= useState(props.default);
    useImperativeHandle(ref, () => ({
      setLabel() {
        setName(props.name);
      }
    }))
  
  
    return (
    <div className='d-flex justify-content-center align-items-center h-80vh'>
        <h1>{name}</h1>
        <button onClick={()=> setName(props.name)}>setName</button>
    </div>) 
})
export default TestComponent;